<template>
  <div>
    <ContentHeader :titulo="titulo"/>

    <section class="content">
      <div class="row">
        <div class="col-sm-10 col-xs-12">
          <LineChart :data="dataSetsensor" :options="optionsChart"/>
        </div>

        <div class="col-sm-10 col-xs-12">
          <LineChart :data="dataSetsensor2" :options="optionsChart2"/>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
// @ is an alias to /src
import ContentHeader from '@/components/dashboard/componentes/Content/ContentHeader'
import LineChart from '@/components/equipos/LineChart'
import SocketClient from '@/services/socketClient'

export default {
  name: 'Maquina1',

  components: {
    ContentHeader,
    LineChart
  },

  computed: {
    dataSetsensor () {
      var data = this.$store.getters['equipo/infoSensor'](this.idsEquipos[0], this.idsSensores[0])
      return {
        labels: data.fecha,
        datasets: [
          {
            label: data.tipo,
            backgroundColor: '#f87979',
            borderColor: '#f87979',
            fill: false,
            data: data.datos
          }
        ]
      }
    },
    dataSetsensor2 () {
      var data = this.$store.getters['equipo/infoSensor'](this.idsEquipos[0], this.idsSensores[1])
      return {
        labels: data.fecha,
        datasets: [
          {
            label: data.tipo,
            backgroundColor: '#f87979',
            borderColor: '#f87979',
            fill: false,
            data: data.datos
          }
        ]
      }
    },
    optionsChart () {
      return { // Chart.js options
        scales: {
          yAxes: [{
            ticks: {
              beginAtZero: true
            },
            gridLines: {
              display: true
            }
          }],
          xAxes: [ {
            gridLines: {
              display: false
            }
          }]
        },
        legend: {
          display: true,
          position: 'bottom'
        },
        title: {
          display: true,
          text: this.$store.getters['equipo/tituloSensor'](this.idsEquipos[0], this.idsSensores[0])
        },
        responsive: true,
        maintainAspectRatio: false
      }
    },
    optionsChart2 () {
      return { // Chart.js options
        scales: {
          yAxes: [{
            ticks: {
              beginAtZero: true
            },
            gridLines: {
              display: true
            }
          }],
          xAxes: [ {
            gridLines: {
              display: false
            }
          }]
        },
        legend: {
          display: true,
          position: 'bottom'
        },
        title: {
          display: true,
          text: this.$store.getters['equipo/tituloSensor'](this.idsEquipos[0], this.idsSensores[1])
        },
        responsive: true,
        maintainAspectRatio: false
      }
    },
    titulo () {
      return this.$store.getters['equipo/tituloEquipo'](this.idsEquipos[0])
    },
    idsSensores () {
      return this.$store.getters['equipo/listaIdsSensores'](this.idsEquipos[0])
    },
    idsEquipos () {
      return this.$store.getters['equipo/listaEquipos'].map((equipo, index) => {
        return equipo.id
      })
    }
  },

  data () {
    return {
      socket: null
    }
  },

  mounted () {
    this.socket = new SocketClient().getClient

    const onConnect = () => {
      console.log('onConnect')
      this.socket.subscribe('prueba/maquina')
    }

    const onConnectFailure = () => {
      console.log('conexion fallida')
    }

    const connectOptions = {
      useSSL: true,
      timeout: 3,
      mqttVersion: 4,
      onSuccess: onConnect,
      onFailure: onConnectFailure
    }

    this.socket.connect(connectOptions)

    this.socket.onConnectionLost = () => {
      console.log('conexion perdida')
    }

    this.socket.onMessageArrived = (msg) => {
      this.$store.dispatch('equipo/sensor', JSON.parse(msg.payloadString))
    }
  },

  beforeDestroy () {
    this.socket.unsubscribe('prueba/maquina')
    this.socket.disconnect()
    this.socket = null
  }
}
</script>
