import EquipoService from '@/services/Equipo'

const state = {
  valvula1In: false,
  valvula1Out: false,
  valvula2In: false,
  valvula2Out: false,
  bomba1: false,
  bomba2: false,
  tanque: 0
}

const getters = {
  getValvula1In: (state) => {
    return state.valvula1In
  },
  getValvula1Out: (state) => {
    return state.valvula1Out
  },
  getValvula2In: (state) => {
    return state.valvula2In
  },
  getValvula2Out: (state) => {
    return state.valvula2Out
  },
  getBomba1: (state) => {
    return state.bomba1
  },
  getBomba2: (state) => {
    return state.bomba2
  },
  getTanque: (state) => {
    return state.tanque
  }
}

const mutations = {
  setValvula1In: (state, valvula1In) => {
    state.valvula1In = valvula1In
  },
  setValvula1Out: (state, valvula1Out) => {
    state.valvula1Out = valvula1Out
  },
  setValvula2In: (state, valvula2In) => {
    state.valvula2In = valvula2In
  },
  setValvula2Out: (state, valvula2Out) => {
    state.valvula2Out = valvula2Out
  },
  setBomba1: (state, bomba1) => {
    state.bomba1 = bomba1
  },
  setBomba2: (state, bomba2) => {
    state.bomba2 = bomba2
  },
  setTanque: (state, tanque) => {
    state.tanque = tanque
  }
}

const actions = {
  async initScada ({ commit }) {
    const response = await EquipoService.getScadaData()
    const data = JSON.parse(response.data.payload).state.reported
    console.log(data)
    const sensores = Object.keys(data)

    sensores.forEach((key) => {
      switch (key) {
        case 'bomba':
          commit('setBomba1', data[key])
          break
        case 'valvula1In':
          commit('setValvula1In', data[key])
          break
        case 'valvula1Out':
          commit('setValvula1Out', data[key])
          break
        case 'valvula2In':
          commit('setValvula2In', data[key])
          break
        case 'valvula2Out':
          commit('/setValvula2Out', data[key])
          break
        case 'bomba2':
          commit('setBomba2', data[key])
          break
        case 'tanque':
          commit('setTanque', data[key])
          break
      }
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
