import uniqid from 'uniqid'
import PahoMQTT from 'paho.mqtt.js'

import SigV4Utils from '@/services/SigV4Utils'

const socketClient = class {
  constructor () {
    const urlEndPoint = SigV4Utils.computeUrl()
    const clientId = uniqid('g-')
    this.client = new PahoMQTT.Client(urlEndPoint, clientId)
  }

  get getClient () {
    return this.client
  }
}

export default socketClient
