import Api from '@/services/Api'
import AutenticationService from '@/services/Authentication'

const equipos = () => {
  return Api().get('/api/equipment/index')
}

const getHistorySensor = (data) => {
  return Api().post('/api/equipment/getHistorySensor', data)
    .catch(async (error) => {
      if (error.response.status === 401) {
        await AutenticationService.refreshToken()
        return getHistorySensor(data)
      }
    })
}

const getScadaData = () => {
  return Api().get('/api/equipment/getInfoScada')
}

export default {
  equipos,
  getHistorySensor,
  getScadaData
}
