<template>
  <Dashboard/>
</template>

<script>
// @ is an alias to /src
import Dashboard from '@/components/dashboard/Dashboard'
import SocketClient from '@/services/socketClient'

export default {
  name: 'home',

  components: {
    Dashboard
  },

  data () {
    return {
      socket: null
    }
  },

  mounted () {
    this.socket = new SocketClient().getClient

    const onConnect = () => {
      console.log('onConnect-Scada')
      this.socket.subscribe('$aws/things/Bomba1/shadow/update/accepted')
    }

    const onConnectFailure = () => {
      console.log('conexion fallida')
    }

    const connectOptions = {
      useSSL: true,
      timeout: 3,
      mqttVersion: 4,
      onSuccess: onConnect,
      onFailure: onConnectFailure
    }

    this.socket.connect(connectOptions)

    this.socket.onConnectionLost = () => {
      console.log('conexion perdida')
    }

    this.socket.onMessageArrived = (msg) => {
      const data = JSON.parse(msg.payloadString).state.reported
      const sensores = Object.keys(data)

      sensores.forEach((key) => {
        switch (key) {
          case 'bomba':
            this.$store.commit('estacionBombeo/setBomba1', data[key])
            break
          case 'valvula1In':
            this.$store.commit('estacionBombeo/setValvula1In', data[key])
            break
          case 'valvula1Out':
            this.$store.commit('estacionBombeo/setValvula1Out', data[key])
            break
          case 'valvula2In':
            this.$store.commit('estacionBombeo/setValvula2In', data[key])
            break
          case 'valvula2Out':
            this.$store.commit('estacionBombeo/setValvula2Out', data[key])
            break
          case 'bomba2':
            this.$store.commit('estacionBombeo/setBomba2', data[key])
            break
          case 'tanque':
            this.$store.commit('estacionBombeo/setTanque', data[key])
            break
        }
      })
    }
  },

  beforeDestroy () {
    this.socket.unsubscribe('$aws/things/Bomba1/shadow/update/accepted')
    this.socket.disconnect()
    this.socket = null
  }
}
</script>
